<template>
  <nav class="">
    <v-toolbar flat="" color="grey lighten-4">
      <!-- <v-app-bar-nav-icon></v-app-bar-nav-icon> -->
      <router-link to="#"> </router-link>

      <v-spacer></v-spacer>
      <v-toolbar-title>
        <span class="font-weight-bold blue--text text--darken-4 mr-2"
          >Mata Funeral Homes</span
        >
        <span class="font-weight-light grey--text"
          >Services Management System</span
        >
      </v-toolbar-title>
      <v-spacer></v-spacer>
    </v-toolbar>

    <!-- nav drawer -->
    <v-navigation-drawer app class="blue darken-2" v-model="drawer">
      <v-list>
        <!-- link 1 -->
        <template v-if="authenticated">
          <v-list-item>
            <v-list-item-avatar>
              <img
                src="data:image/jpeg;base64,"
              />
            </v-list-item-avatar>
            <v-list-item-content>
              <!-- <template v-if="authenticated"> -->
              <v-list-item-title class="white--text text--lighten-4 "
                >Hello! {{ user.name }}</v-list-item-title
              >
              <v-list-item-subtitle class="blue--text text--lighten-3"
                >Logged as: Cantilan User</v-list-item-subtitle
              >
            </v-list-item-content>
          </v-list-item>
          <v-divider color=""></v-divider>
          <v-list-item
            v-for="link in links"
            v-bind:key="link.icon"
            route
            :to="link.route"
          >
            <v-list-item-icon>
              <v-icon class="blue--text text--lighten-4">{{
                link.icon
              }}</v-icon>
            </v-list-item-icon>
            <v-list-item-content>
              <v-list-item-title class="blue--text text--lighten-4">{{
                link.name
              }}</v-list-item-title>
            </v-list-item-content>
          </v-list-item>
          <!-- signout -->
          <v-list-item @click="signOut" route>
            <v-list-item-icon>
              <v-icon class="blue--text text--lighten-4">
                exit_to_app
              </v-icon>
            </v-list-item-icon>
            <v-list-item-content>
              <v-list-item-title class="blue--text text--lighten-4">
                Sign-Out
              </v-list-item-title>
            </v-list-item-content>
          </v-list-item>
        </template>
        <!-- link 2 -->
        <template v-else>
          <v-list-item>
            <v-list-item-avatar>
              <img
                src="data:image/jpeg;base64,"
              />
            </v-list-item-avatar>
            <v-list-item-content>
              <!-- <template v-if="authenticated"> -->
              <v-list-item-title class="white--text text--lighten-4 "
                >Welcome to MSMS</v-list-item-title
              >
              <v-list-item-subtitle class="blue--text text--lighten-3"
                >Mata Services Management System</v-list-item-subtitle
              >
            </v-list-item-content>
          </v-list-item>
          <v-divider color=""></v-divider>
          <v-list-item
            v-for="link in links2"
            v-bind:key="link.icon"
            route
            :to="link.route"
          >
            <v-list-item-icon>
              <v-icon class="blue--text text--lighten-4">{{
                link.icon
              }}</v-icon>
            </v-list-item-icon>
            <v-list-item-content>
              <v-list-item-title class="blue--text text--lighten-4">{{
                link.name
              }}</v-list-item-title>
            </v-list-item-content>
          </v-list-item>
        </template>
      </v-list>
    </v-navigation-drawer>
  </nav>
</template>

<script>
import { mapGetters, mapActions } from "vuex";
export default {
  name: "Navbar",
  computed: {
    ...mapGetters({
      authenticated: "auth/authenticated",
      user: "auth/user",
    }),
  },
  data() {
    return {
      drawer: true,
      links: [
        { icon: "dashboard", name: "Dashboard", route: "/", small: false },
        // {
        //   icon: "attach_money",
        //   name: "Payments",
        //   route: "/payments",
        //   small: false,
        // },
        {
          icon: "assignment",
          name: "Summary",
          route: "/summary",
          small: false,
        },
      ],
      links2: [
        { icon: "login", name: "Sign-in", route: "/signin", small: true },
        { icon: "book", name: "About", route: "/about", small: true },
      ],
    };
  },
  methods: {
    ...mapActions({
      signOutActions: "auth/signOut",
    }),
    signOut() {
      this.signOutActions().then(() => {
        this.$router.push(`/signin`);
      });
    },
  },
};
</script>
<style scoped></style>
